import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  restInfo: {
    marginTop: "20px",
    paddingBottom: "15px",
    borderRadius: "0px",
    border: "none",
    boxShadow: "none",
    borderBottom: "0.7px solid lightgrey",
  },
  main_text: {
    textAlign: "center",
    fontWeight: "600",
    color: "#3d4152",
    paddingTop: "8px",
    textTransform: "capitalize",
    opacity: 0.9,
    fontSize: "1.2rem",
    marginTop: "9px",
  },
  light_text: {
    textAlign: "center",
    fontSize: "0.93rem",
    color: "#535665",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
    overflow: "hidden",
    fontWeight: "bold",
    opacity: 0.56,
  },
  icon_img_div: {
    textAlign: "center",
  },
  icon_img: {
    width: "60px",
    padding: "4px",
    border: "1px solid lightgray",
    borderRadius: "7px",
    background: "#f7f2f2",
  },
  dottedSeperator: {
    border: "none",
    borderBottom: "10px solid #ececec",
    width: "100%",
    margin: "20px auto 15px auto",
  },
  infoGrid: {
    borderRight: "1px solid lightgray",
  },
  searchBtn: {
    marginLeft: "auto",
    padding: "6px",
    borderRadius: "50%",
    boxShadow: "0 0 8px -1px lightgrey",
    "&:active": {
      boxShadow: "none",
      backgroundColor: "lightgray",
    },
  },
});

export default useStyles;
